
html, body {
  height: 100%;
  width: 100%;
  margin:0;
}

#root, #wrapper {
  height: 100%;
}

.navbar {
  background:#325d81;
  position: fixed;
  top: 0;
  width: 100%;
  z-index:5;
}

.main {
  width: 100%;
  min-height: 100% !important;
  /* min-height: 700px; */
  position: relative;
  margin-bottom:-50px;
  }

.main::before {
  background-image: url("../bg.jpg");
  background-size: cover;
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  z-index: -2;
  opacity: 0.2;
  margin-bottom:-50px;
}

i:hover {
  color: #17a2b8 !important;
}

.table {
  margin-top: 1rem;
}

thead {
  background-color:#325d81;
}

#searchBox {
  border: 1px solid #17a2b8;
}

#searchBox:focus {
  border:1px solid #17a2b8;
  box-shadow: 0 0 10px #17a2b8;
}

.border-info {
border-width: 1px !important;
}

.btn-info {
  background-color: #325d81 !important;
}
/* #2d2d2d */
.btn-info:hover {
  background-color: #17a2b8 !important;
}

.btn-info:focus {
  background-color: #17a2b8 !important;
}

a {
  color: #17a2b8;
}

.list-container {
  /* padding-top:calc(5% + 33px); */
  margin-bottom: 5%;
}

.card-container {
  text-align: center;
  /* margin: 0 auto; */
  /* padding-top: + 5%; */
  margin-bottom: 5%;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 400px;
  margin: 1rem;
  position: relative;
  width: 350px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  display: inline-block;
  padding-bottom: 0px;
  text-align:left;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:#325d81;
  padding-top: 2px;
}
.card > .img-container > img {
  width: 70%;
  height: auto;
}

.card > .content {
  padding-left: .7rem;
  padding-right: .7rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
  padding-top:10px;
}

.card > .content > ul > li {
  margin-top: 0px;
  padding-top: 10px;
}

.push, .footer {
  height: 50px;
}

.footer {
  background-color:rgb(56,56,56);
  color:white;
  text-align: center;
  padding-top: 10px;
}



@media screen and (max-width: 775px) {

  .btn {
    font-size: 10px !important;
    padding:2px !important;
    margin:0 !important;
  }
  table {
    width: 100% !important;
    font-size: 10px;
  }
}

@media screen and (max-width: 450px) {
  .card {
    margin: 0;
    margin-top:1rem;
    margin-bottom:.5rem;
    padding:0;
    width: 95%;
  }
}